import React from "react";

const Logout = React.lazy(() => import("./views/pages/Logout/Logout"));
const Dashboard = React.lazy(() => import("./views/Dashboard"));

const Item = React.lazy(() => import("./views/AppData/Item"));
const AddItem = React.lazy(()=>import('./views/AppData/Item/Add'));
const Dropzone = React.lazy(() => import("./views/AppData/DropZone"));
const Category = React.lazy(() => import("./views/AppData/Category"));
const SubCategory = React.lazy(() => import("./views/AppData/SubCategory"));
const Provinsi = React.lazy(() => import("./views/AppData/Lokasi/Provinsi"));
const Kabupaten = React.lazy(() => import("./views/AppData/Lokasi/Kabupaten"));
const Kecamatan = React.lazy(() => import("./views/AppData/Lokasi/Kecamatan"));

const PendingOrder = React.lazy(() => import("./views/PendingOrder"));
const Order = React.lazy(() => import("./views/Order"));
const Transaction = React.lazy(() => import("./views/Transaction"));
const TransactionDetail = React.lazy(() => import("./views/TransactionDetail"));

const Customer = React.lazy(() => import("./views/User/Customer"));

const routes = [
  { path: "/admin/dashboard", name: "Dashboard", component: Dashboard },
  { path: "/admin/data/item", name: "Item", component: Item },
  { path: "/admin/add/item",name:"Add Item",component: AddItem},

  { path: "/admin/data/category", name: "Categories", component: Category },
  {
    path: "/admin/data/subcategory/:category_id",
    name: "Categories",
    component: SubCategory
  },
  { path: "/admin/data/dropzone", name: "Drop zone", component: Dropzone },
  { path: "/admin/data/provinsi", name: "Provinsi", component: Provinsi },
  {
    path: "/admin/data/kabupaten/:provinsi_id",
    name: "Kabupaten",
    component: Kabupaten
  },
  {
    path: "/admin/data/kecamatan/:kabupaten_id",
    name: "Kecamatan",
    component: Kecamatan
  },
  {
    path: "/admin/pendingOrder",
    name: "Pending Order",
    component: PendingOrder
  },
  { path: "/admin/Order", name: "Order", component: Order },
  { path: "/admin/Transaction", name: "Transaction", component: Transaction },
  {
    path: "/admin/TransactionDetail/:transaction_id",
    name: "Transaction Detail",
    component: TransactionDetail
  },

  { path: "/admin/user/customer", name: "Customer", component: Customer },
  { path: "/admin/logout", name: "logout", component: Logout }
];

export default routes;
